* {
  box-sizing: border-box;
  font-family: 'Ubuntu Condensed', 'Vidaloka', 'Cardo', sans-serif, serif;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;

}

html {
  --blue: 5, 46, 66;
  --darkTeal: 0, 47, 58;
  --font: rgba(213, 213, 213, 0.861);
  --fontBlue: 7, 29, 41;
  --grey: 93, 96, 109;
  --orange: 255, 72, 0;
  --teal: 1, 120, 120;
  font-size: 62.5%;
}

body {
  min-height: 100vh;
  overflow-x: hidden;
  width: 100%;
  background: radial-gradient(circle at center,
      rgb(17, 46, 50), transparent),
    #042438;
}

h2 {
  letter-spacing: 0.2ch;
}

p {
  letter-spacing: 0.3ch;
  line-height: 3.8ch;
}

a {
  text-decoration: none;
  color: inherit;
}

