.glass {
  backdrop-filter: blur(150px);
  -webkit-backdrop-filter: blur(150px);
  border: 2px solid rgb(255, 255, 255, 0.2);
  box-shadow: 0px 0px 3rem rgb(0, 0, 0, 0.8);
  z-index: 3;
}

.ball {
  border-radius: 50%;
  position: absolute;
  z-index: -5;
  box-shadow: 0px 0px 150px 10px rgb(0, 0, 0, 0.9);
}

.ballRotate {
  animation: rotate 24s infinite linear forwards 1.5s;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

button {
  outline: none;
  cursor: pointer;
}

.invisible {
  opacity: 0;
}

.visible {
  opacity: 1;
}

::-webkit-scrollbar {
  background: inherit;
  width: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(var(--orange), 0.5);
  border-radius: 30rem;
  border: 3px solid rgb(var(--darkTeal));
  height: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}

::-webkit-scrollbar-track {
  border-radius: 100%;
}